import { PreloadFile } from "utils/file";
import PreloadedFilesPlash from '../preloaded-files-plash/plash';

import styled from "styled-components";

const SClipPlhs = styled.div`
    position: absolute;
    left: -100vw;
    bottom: 100%;
    background: rgba(255, 255, 255, 0.54);
    border-radius: 10px;
    transition: var(--transitionApp);

    & > * {
        margin: 1rem .5rem;
    }
`;

const SClipPlshWrapper = styled.div`
    position: relative;

    &:hover ${SClipPlhs} {
        left: 0;
    }
`;

export default function ClipPlash({Wrapper, preloadedFiles = [], setFiles = ()=>{}}) {
    const addToPlash = (...files) => setFiles([...preloadedFiles, ...files]);
    const removeFile = filename => setFiles(preloadedFiles.filter(file => file.filename !== filename))

    const preloadedCB = (file, src) => {
        addToPlash({
            'type': "image",
            'file': file,
            'src': src,
            'filename': file.name,
        });
    }

    return (
        <>
            <PreloadedFilesPlash preloadedFiles={preloadedFiles} removeFile={removeFile} />

            <SClipPlshWrapper>
                <SClipPlhs>
                    <Wrapper alt="clip" srcIcon="/img/send-gallery.png" onClick={() => PreloadFile('image/*', preloadedCB)} />
                </SClipPlhs>
                <Wrapper alt="clip"  srcIcon="/img/clip.png" />
            </SClipPlshWrapper>
        </>
    )
}